.site-header {
  box-shadow: 0 15px 15px -5px rgba(0,0,0,0.1);
  position: relative;
  z-index: 4;
}

.site-header > .site-width {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.site-header-list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  @include desktop {
    align-items: center;
    flex-direction: row;
  }
}

.site-navigation--header {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  top: 5.5rem;
  z-index: 2;
  background-color: #fff;
  @include desktop {
    position: static;
    top: auto;
    background-color: transparent;
    display: block;
  }

  form {
    &.mobile-search {
      @include desktop {
        display: none;
      }
    }
    &.desktop-search {
      display: none;
      @include desktop {
        display: block;
      }
      input {
        background-color: #fff;
        font-size: 0.8rem;
        border-radius: 5px;
        width: 200px;
      }
    }
  }

  input {
    width: calc(100% + 2rem);
    width: 100%;
    position: relative;
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    font-size: 1rem;
    padding: 1rem;
    background-color: $light-gray;
    border: 0;
    -webkit-appearance: none;
  }

  .menu-open & {
    display: block;
  }


}

.site-header .menu-item--logo-container img {
  width: 100%;
  max-width: 225px;
  display: block;
}

.menu-item--logo-container a {
  display: block;
  padding: 1rem 0;
  @include desktop {
    padding: 0;
  }
}

.site-header-list .menu-item--logo-container a:after {
  display: none;
}

.site-header-list li {
  border-bottom: 1px solid $medium-gray;
  @include desktop {
    padding-left: 0;
    padding-right: 0;
    border-bottom: 0;
  }
}

.site-header-list > li {
  @include desktop {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;

    > a:after {
      content: '|';
      display: inline-block;
      margin-left: 0.5rem;
    }
    
    &:hover > .accordian-toggle--site-header:before {
      content: url(../img/nav--active-brand-two.svg);
      position: absolute;
      width: 25px;
      left: 50%;
      top: calc(100% + 9px);
      transform: translateX(-50%);
    }
    
    .accordian-toggle--site-header {
      @include desktop {
        position: relative;
      }
    }

  }

}

.site-header-list a {
  display: block;
  color: $brand;
  text-decoration: none;
  padding: 1rem;
  @include desktop {
    margin: 0 0.5rem;
    padding: 0;
  }
}

.site-header-list > a:after {
  content: url(../img/accordian-toggle.svg);
  float: right;
  @include desktop {
    content: '|';
    display: inline-block;
    margin-left: 0.5rem;
    margin-right: -0.5rem;
  }
}

.site-header-list li:last-child a:after {
  display: none;
}

.menu-item--menu-toggle {
  @include desktop {
    display: none;
  }
}

.accordian-toggle--site-header {
  display: block;
  background-color: transparent;
  border: 0;
  padding: 1rem;
  width: 100%;
  text-align: left;
  color: $brand;
  font-size: 1rem;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  transition: all 250ms ease-in-out;
  @include desktop {
    max-width: 200px;
    padding: 0.5rem;
  }

  &:after {
    content: url(../img/accordian-toggle.svg);
    width: 20px;
    float: right;
    transform: rotateZ(-0deg);
    transition: transform 250ms ease-in-out;
    @include desktop {
      width: auto;
      content: '|';
      display: inline-block;
      margin-left: 0.5rem;
      margin-right: -0.5rem;
      transform: rotateZ(0);
    }
  }
  &:focus {
    outline: 0;
  }
  .sub-menu--level-one & {
    padding-left: 2rem;
  }
}

.accordian-toggle--site-header.active {
  background-color: $brand;
  color: #fff;
  @include desktop {
    background-color: transparent;
    color: $brand;
  }
  
  &:after {
    transform: rotateZ(180deg);
    @include desktop {
      transform: rotateZ(0);
    }
  }

}

[class*="sub-menu"] {
  display: none;
  list-style-type: none;
  padding-left: 0;
  &.accordian-open {
    display: block;
    @include desktop {
      display: none;
    }
  }
  > li:first-child {
    border-top: 1px solid $medium-gray;
    @include desktop {
      border-top: 0;
    }
  }
  .menu-item-has-children:hover > & {
    @include desktop {
      display: block;
    }
  }
}

.sub-menu--level-one {
  @include desktop {
    background-color: $light-gray;
    position: absolute;
    left: 0;
    right: 0;
    transform: translateY(calc(1rem + 5px));
    border-top: 5px solid $brand-two;
    border-bottom: 5px solid $brand-two;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    box-shadow: 0 10px 25px 0px rgba(black, 0.3);
  }
}

.sub-menu--level-one > li {
  @include desktop {
    max-width: 1080px;
    margin: 0 auto;
  }

  button {
    @include desktop {
      border-bottom: 1px solid $medium-gray;
    }
  }

}

.sub-menu--level-one .accordian-toggle--site-header {
  &:after {
    @include desktop {
      display: none;
    }
  }
  &.active:after {
    @include desktop {
      display: block;
      content: url(../img/nav--active.svg);
      position: absolute;
      top: 0;
      left: calc(200px - 1.7rem);
      width: 2.4rem;
    }
  }
}

.sub-menu--level-one .accordian-toggle--site-header.active {
  border-bottom: 1px solid $light-gray;
}

.sub-menu--level-one.accordian-open > li:first-child {
  border-top: 5px solid $brand-two;
  @include desktop {
    border-top: 0;
  }
}

.sub-menu--level-one.accordian-open > li:last-child {
  border-bottom: 5px solid $brand-two;
  @include desktop {
    border-bottom: 0;
  }
}

.sub-menu--level-two {
  background-color: $light-gray;
  padding: 0 1rem;
  @include desktop {
    position: absolute;
    z-index: 2;
    width: 100%;
    max-width: calc(1080px - 200px);
    top: 5rem;
    bottom: 1.5rem;
    transform: translateX(200px);
    padding: 0;
    padding-left: 4rem;
    display: flex !important;
  }

  > li {
    padding: 2rem 0 1rem;
    @include desktop {
      padding-top: 0;
      flex-basis: calc(100% / 3);
    }

    &:first-child {
      border-top: 0;
    }
    &:last-child {
      border-bottom: 0;
    }
  }

  .menu-item--equipment & {
    text-align: center;
    @include desktop {
      flex-wrap: wrap;
    }
  }

}

.sub-menu--level-two h6 {
  color: $brand;
  font-size: 1.25rem;
  margin-bottom: 0.5rem;
}

.sub-menu--level-two.accordian-open .sub-menu--level-three {
  display: flex;
  flex-wrap: wrap;
  @include desktop {
    display: list-item;
  }

  li {
    border: 0;
    flex-basis: 50%;
  }
  
  a {
    color: $black;
    padding: 0 0 1em;
    font-size: 0.9rem;
    @include desktop {
      &:hover {
        text-decoration: underline;
      }
    }
  }

}

// .menu-item--equipment .sub-menu--level-two {
//   align-items: flex-end;
// }

.menu-item--equipment .sub-menu--level-two > li {
  padding: 0;
}

.menu-item--equipment .sub-menu--level-two > li > a {
  border: 1px solid $medium-gray;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 2px 2px 6px rgba(black, 0.05);
  padding: 1rem;
  transition: all 250ms ease-in-out;
  min-height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &:hover {
    background-color: #fff;
    text-decoration: none !important;
    box-shadow: 5px 5px 6px rgba(black, 0.15);
    transform: scale(1.02) translate(-3px, -3px);
  }
}

.menu-item--equipment .sub-menu--level-two img {
  max-height: 75px;
  max-width: 100px;
}

.menu-item--equipment .sub-menu--level-two h6 {
  margin-bottom: 0;
}


/*
=========================
DESKTOP INTERACTIVITY
=========================
*/
@include desktop {

.menu-item--top-level:hover {
  
  .sub-menu--level-one {
    display: block;
    min-height: 50vh;
    
    li button {
      transition: unset;
    }
    
    li:hover {
      button {
        background-color: $brand;
        color: #fff;
        &:after {
          content: url(../img/nav--active.svg);
          display: block;
          position: absolute;
          top: 0;
          bottom: 0;
          left: calc(100% - 27px);
          z-index: 3;
          width: 38px;
        }
      }
      .sub-menu--level-two {
        display: flex !important;
      }
      .sub-menu--level-three {
        display: list-item;
        z-index: 3;
      }
    }

  }

  .sub-menu--level-two {
    display: none !important;
    a:hover {
      text-decoration: underline;
    }
  }

}

}


/*
=========================
CART CONTAINER
=========================
*/
.cart-container {
  display: none;
  @include desktop {
    display: block;
  }

  a {
    position: relative;
  }
  
  strong {
    position: absolute;
    top: -10px;
    right: 0px;
    font-size: 1.25rem;
  }

}